import React,{ useEffect, useState} from 'react';
import './css/style.css'

const Temp = ()=>{

    const [City, setCity] = useState();                 // Searched City Available
    const [Search , setSearch] = useState("London");    // Search City

    useEffect( ()=>{
        const fetchApi = async ()=>{
        const Api = `http://api.openweathermap.org/data/2.5/weather?q=${Search}&units=metric&appid=d4fc24841b4e33f2e7c3f5ad31816c4d`;
        const Response = await fetch(Api);
        //console.log(Response);
        const Json_Response = await Response.json();
        //console.log(Json_Response);
        setCity(Json_Response.main);
        }

        fetchApi();
        
    },[Search])
    return(                                 // User Jo v data daalega wo onChange m save ho rha h // 
        <div className="main">
        <section className="box">               
            <input type="search" className="search_box" onChange ={(event)=>{setSearch(event.currentTarget.value)}}></input> 


            {!City ? (<h3>No Data Found!!</h3>) : (
                <div className="content"> 
                <div className="center">
                <h1 className="city_name">{Search}</h1>
            </div>
            <div className="footer">
            <h3 className="city_temp">{City.temp}</h3>
            <span className="min-max">
                <div>Min Temp<h4 className="min">{City.temp_min}</h4></div> <p className="line">|</p>
                <div>Max Temp<h4 className="max">{City.temp_max}</h4></div>
            </span>
            </div>
            </div>
            )}


        </section>
        </div>
    )
}

export default Temp;